import { css } from '@emotion/react';
import AtomLoader from '../../AtomLoader';

const LoaderSmall = () => {
  return (
    <AtomLoader
      isLoading
      type="small"
      colorLoading="#a5a5a5"
      widthLoader="2px"
      customCSS={css`
        width: 100%;
        .lds-ring {
          width: 15px;
          height: 15px;
          div {
            margin: 1px 2px;
            width: 14px;
            height: 14px;
          }
        }
      `}
    />
  );
};

export default LoaderSmall;
